.speedbar {
	display: flex;
	flex-wrap: wrap;
	margin-bottom: rem(30);
	width: 100%;

	a, span {
		color: #484848;
		font-size: rem(14);
	}
	a {
		margin-right: rem(24);
		position: relative;

		&:hover {
			text-decoration: underline;
		}

		&::after {
			content: ">";
			color: $blue2;
			position: absolute;
			right: rem(-15);
			pointer-events: none;
		}
	}
}

.form-search {
	position: relative;
	margin-bottom: rem(30);

	.field {
		padding-left: rem(40);
		padding-right: rem(80);
	}

	.button-blue {
		position: absolute;
		top: 0;
		right: 0;
	}

	.icon-loupe {
		position: absolute;
		top: rem(12);
		left: rem(10);
		font-size: rem(18);
	}
}

.item-cart {
	border-radius: rem(5);
	overflow: hidden;
	display: flex;
	width: rem(276);
	margin-bottom: rem(20);

	.left {
		display: flex;
		align-items: center;
		justify-content: center;
		background-color: $gray;
		flex: 0 0 rem(83);
		height: rem(97);

		i {
			color: $blue2;
			font-size: rem(42);
		}

		.icon-promotion {
			font-size: rem(52);
		}
	}

	.right {
		border-top-right-radius: rem(5);
		border-bottom-right-radius: rem(5);
		border: rem(1) solid #dbdbdb;
		display: flex;
		flex-direction: column;
		justify-content: center;
		padding: 0 rem(16);

		b {
			margin-bottom: rem(8);
		}
	}
}

.page-h2 {
	margin-bottom: rem(40);
}

.item-querstion {
	display: flex;

	width: 31.333%;

	margin-right: rem(30);
	margin-bottom: rem(30);

	@include min660 {
		width: 100%;
		margin-right: 0;
	}

	&:nth-child(3n) {
		margin-right: 0;
	}

	.left {
		flex: 0 0 rem(20);
		margin-right: rem(5);

		img {
			margin-top: rem(3);
		}
	}
	.right {
		
		b {
			display: block;
			font-size: rem(18);
			font-weight: 600;
			margin-bottom: rem(5);
		}

		p {
			font-weight: 300;
		}
	}
}

.signup-list {
	> b {
		display: block;
		font-size: rem(18);
	}
	> span {
		color: #6a6a6a;
		margin-bottom: rem(6);
		display: block;
	}

	ol {

		li {
			// margin-left: rem(15);
			font-weight: 300;

			b {
				font-weight: normal;
			}
		}
	}
}

.w-260 {
	width: rem(260);
}

.text-18 {
	font-weight: 600;
	font-size: rem(18);
}

.b-white {
	border-radius: rem(5);
	background-color: white;
	border: rem(1) solid #dbdbdb;
	padding: rem(10);

	ul {

		li {

			margin-bottom: rem(20);

			&:last-child {
				margin-bottom: 0;
			}

			img {
				margin-right: rem(5);
			}

			a {
				display: flex;
				align-items: center;
				font-weight: 300;

				&:hover {
					text-decoration: underline;
					color: $blue2;
				}
			}
		}
	}
}
.tablet.mt-30 {
	@include min660 {
		margin-top: rem(30);
	}
}
.tablet {
	width: rem(372);
	border-radius: rem(5);
	border: rem(1) solid #dbdbdb;
	background-color: white;

	@include min660 {
		width: 100%;
	}

	&-title {
		background-color: $gray;
		padding: rem(20) rem(10) rem(12) rem(10);
		font-size: rem(18);
		font-weight: 600;
	}

	&-items {
		padding: rem(20) rem(10) rem(12) rem(10);

		display: flex;
		flex-direction: column;

		li {

			margin-bottom: rem(20);

			&:last-child {
				margin-bottom: 0;
			}

			img {
				margin-right: rem(5);
			}

			a {
				display: flex;
				align-items: flex-start;
				font-weight: 300;

				&:hover {
					text-decoration: underline;
					color: $blue2;
				}
			}
		}
	}
}

.faq {
	position: absolute;
	width: rem(456);
	height: rem(392);
	right: 0;
	z-index: -1;
	margin-top: rem(60);

	@include min768 {
		position: relative;
		width: 100%;
		margin-top: 0;
		margin-bottom: rem(40);
		height: auto;
	}

	.overlay {
		position: absolute;
		top: 0;
		right: -17%;
		z-index: -2;
	}
}

.faq-bg {
	position: absolute;
	top: 0;
	right: 0;

	width: 100%;

	@include min768 {
		position: relative;
	}
}