.popup {
	background-color: rgba(0,0,0,.75);
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 301;

	display: flex;
	align-items: center;
	justify-content: center;

	display: none;

	&-wrap {
		width: rem(360);
		border: rem(1) solid #dbdbdb;
		background-color: white;
		border-radius: rem(5);
		padding: rem(20) rem(10);
		text-align: center;

		@include min540 {
			width: 100%;
			height: 100%;
			border: 0;
			border-radius: 0;
			overflow-y: scroll;
		}

		&_header {
			margin-bottom: rem(24);

			span {
				font-size: rem(26);
				font-weight: 600;
			}
		}

		&_form {

			.field,
			.custom-select,
			.field-textarea {
				margin-bottom: rem(10);
			}

			.field-group-text .field {
				margin-bottom: 0;
			}

			.field-textarea {
				font-style: normal;
			}

			.shortstory {
				min-height: rem(64);
			}

			.button-gray {
				display: none;

				@include min540 {
					display: block;
					margin-left: rem(10);
				}
			}
		}
	}

	
}

.popup.active {
	display: flex;
}