.order {
	display: flex;
	flex-direction: column;
	margin-top: rem(24);

	&-bottom {
		
		display: flex;
		flex-wrap: wrap;
		
		.item {
			width: 50%;
			background-color: $gray;
			padding: rem(20);

			@include min660 {
				width: 100%;
				margin-bottom: rem(20);

				&:last-child {
					margin-bottom: 0;
				}
			}

			b {
				display: block;
				margin-bottom: rem(3);
			}
		}
	}

	&-item {
		margin-bottom: rem(20);

		b {
			margin-bottom: rem(3);
			display: block;
		}
	}

	&-table {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		margin-bottom: rem(28);

		.item {
			display: flex;
			flex-direction: column;
			width: 33.333%;

			b {
				margin-bottom: rem(3);
			}
			
		}
		.item.last {

			@include min {
				text-align: right;
			}
		}
	}

	&-comment {
		margin-top: rem(44);

		@include min660 {
			margin-top: rem(20);
			margin-bottom: rem(50);

			.smart-block {
				flex-wrap: wrap;
			}
		}

		.title {
			font-weight: 600;
			font-size: rem(24);
			margin-bottom: rem(30);
		}

		.text {
			margin: rem(30) 0;

			@include min660 {
				margin: rem(10) 0;
			}
		}

		.ansfer {
			display: block;
			margin-bottom: rem(20);
		}

		.rating {
			display: flex;
			flex-wrap: wrap;

			@include min660 {
				margin-top: rem(10);
				margin-left: rem(40);
			}

			i {
				font-size: rem(20);
				margin-right: rem(5);

				&:last-child {
					margin-right: 0;
				}
			}
		}
	}
}

