.form {
	display: flex;
	flex-direction: column;

	.link {
		font-size: rem(14);
		font-weight: 300;
		color: $blue2;
	}

	&-social {
		display: flex;
		flex-direction: column;
		align-items: center;

		.title {
			font-weight: 300;
			margin: rem(28) 0;
		}

		&_items {
			display: flex;
			flex-wrap: wrap;
			
			justify-content: space-between;
		}

		.item {
			position: relative;
			color: white;
			border-radius: rem(5);
			background-color: $blue2;
			padding: rem(12);
			text-align: center;
			
			// align-items: center;
			width: rem(165);
			margin-bottom: rem(15);

			@include min660 {
				width: 48%;
			}

			span {
				font-weight: 300;
			}

			i {
				position: absolute;
				top: rem(12);
				left: rem(12);
				color: white;
				font-size: rem(20);

				&::before {
					color: white;
				}
			}
			
			.icon-google {
				font-size: rem(25) !important;
			}

			.icon-vk {
				font-size: rem(16) !important;
			}
		}
	}

	&_group {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		margin-bottom: rem(12);

		.fieldset,
		.field-group-text {

			width: calc(50% - 0.9375rem);

			@include min660 {
				width: 100%;

				margin-bottom: rem(12);

				&:last-child {
					margin-bottom: 0;
				}
			}

			
		}
	}
	
	> .fieldset {
		margin-bottom: rem(12);
	}


}


.bg-abs {
	&::before {
		content: "";
		position: absolute;
		top: 0;
		right: -30%;
		z-index: -1;
		width: rem(683);
		height: 100%;
		background: url(../img/right-bg-2.png) no-repeat;
		background-size: cover;
		
		@include min {
			display: none;
		}
	}
}