.profile {
	display: flex;
	flex-wrap: wrap;

	&-title {
		flex: 0 0 rem(200);

		@include min660 {
			flex: 0 0 100%;
			margin-bottom: rem(30);
		}
	}

	&-menu {
		display: flex;
		// flex-wrap: wrap;

		img {
			align-self: flex-start;
			flex: 0 0 rem(68);
			margin-right: rem(30);
		}

		ul {

			li {
				margin-bottom: rem(10);

				&:last-child {
					margin-bottom: 0;
				}

				a {
					width: rem(190);
					background-color: $gray;
					border-radius: rem(5);
					font-weight: 300;
					display: block;
					text-align: center;
					padding: rem(12) 0;

					&:hover {
						background-color: $blue2;
						color: white;
					}
				}
			}
		}
	}
}

.item-list {
	display: flex;
	flex-direction: column;
	margin-bottom: rem(26);

	&_gray {
		background-color: #eeeded;
		border-radius: rem(5);
		padding: rem(14);
		display: flex;
		flex-wrap: wrap;
		align-items: center;

		@include min660 {
			flex-wrap: nowrap;
			
		}

		.icon-danger {
			margin-right: rem(10);
			color: $blue2;
			font-size: rem(20);

			@include min660 {
				align-self: flex-start;
			}
		}
	}

	b {
		margin-bottom: rem(14);
	}

	p {
		margin-bottom: rem(8);
	}

	.button-blue, .button-gray {
		align-self: flex-start;
	}
}