.footer {
	// position: relative;
	margin-top: rem(44);
	background-color: $gray;
	height: rem(84);

	> .container {
		position: relative;
		&::before {
			content: "";
			position: absolute;
			top: rem(-140);
			z-index: -1;
			left: -10%;
			background: url(../img/footer-line.png) no-repeat;
			width: rem(82);
			height: rem(151);

			@include min {
				display: none;
			}
		}
	}


	@include min {
		height: auto;
		padding-top: rem(40);
		padding-bottom: rem(30);
	}
	
	// margin-top: auto;

	> .container {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: center;
		height: 100%;

		@include min {
			flex-direction: column;

			>span {
				order: 999;
			}
		}
	}

	&_logo {

		@include min {
			margin-bottom: rem(26);
			order: 2;
		}
	}

	

	&_nav {
		display: flex;
		flex-wrap: wrap;
		margin-left: auto;

		@include min {
			margin-left: 0;
			margin-bottom: rem(35);
			order: -999;
		}

		li {
			position: relative;
			margin-right: rem(36);

			@include min {
				margin-right: 0;

				&:first-child {
					margin-right: rem(40);
				}
			}

			a {
				display: flex;
				align-items: center;

				i {
					position: absolute;
					left: rem(-24);
					top: rem(4);
					color: $blue;
					font-size: rem(18);
				}

				&:hover {
					color: $blue;

					i {
						color: black;
					}
				}
			}
		}
	}
}