.user-block {
	display: flex;
	flex-direction: column;

	@include min660 {
		margin-top: rem(20);
	}

	&_top {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		margin-bottom: rem(20);

		@include min {
			justify-content: flex-start;
		}

		.avatar {
			flex: 0 0 rem(100);
			margin-right: rem(14);
		}

		.center {
			margin-right: auto;
			display: flex;
			flex-direction: column;

			@include min540 {
				margin-right: rem(10);
				margin-left: auto;
				width: rem(150);
			}

			> .flex {
				margin-top: rem(18);
				justify-content: space-between;

				@include min768 {
					justify-content: flex-start;
				}
				
				.item {
					display: flex;
					flex-wrap: wrap;
					align-items: center;
					margin-right: rem(12);
					font-size: rem(14);

					.icon-checked {
						color: #03a507;
					}

					.icon-transfer {
						color: $blue2;
					}

					@include min768 {
						&:first-child {
							width: 100%;
							margin-bottom: rem(8);
						}
					}

					&:last-child {
						margin-right: 0;
					}

					a {
						font-weight: 700;
						color: $blue2;
					}

					img, a, i {
						margin-right: rem(6);
					}
				}
			}
		}

		.right {
			flex: 0 0 rem(180);
			display: flex;

			@include min540 {
				margin-top: rem(10);
				margin-left: auto;
			}

			img {
				flex: 0 0 rem(52);
				align-self: flex-start;
				margin-right: rem(16);

				@include min540 {
					flex: 0 0 rem(34);
					width: rem(34);
					margin-right: rem(6);
				}
			}

			> .flex {
				display: flex;
				flex-direction: column;

				a {
					color: $blue2;
					margin-top: rem(3);
				}
			}
		}
	}

	&_center {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: center;
		margin-bottom: rem(24);

		@include min {
			margin-left: -20px;
			margin-right: -20px;
			padding: 0 20px;
		}

		ul {
			width: 25%;

			@include min768 {
				width: 100%;
				margin-bottom: rem(6);
			}
			li {
				margin-bottom: rem(3);

				&:last-child {
					margin-top: 0;
				}
			}
		}

		.calc {
			width: 35%;
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			margin-right: rem(20);

			@include min768 {
				width: 100%;
				margin-right: 0;
				margin-bottom: rem(20);
			}

			> span {
				margin-right: rem(8);
			}

			.field-number {
				background-color: white;
				width: rem(54);
				height: rem(42);
				border-radius: rem(5);
				display: flex;
				align-items: center;
				border: rem(1) solid #dbdbdb;
				justify-content: space-between;

				button {
					font-size: rem(24);
					font-weight: bold;
				}

				input {
					width: 100%;
					text-align: center;
					font-size: rem(14);
				}
			}

			
		}

		.price {
			width: 35%;

			@include min768 {
				width: 100%;
			}
		}
	}

	.form-pay {

		@include min540 {
			.sm-flex-wrap {
				justify-content: flex-end;
				.button-blue {
					margin-top: rem(20);
					width: 100%;
					text-align: center;
				}
			}
		}

		.field {
			margin-bottom: rem(20);
		}

		.round-list {
			margin-top: rem(20);
			display: flex;
			justify-content: space-between;

			@include min540 {
				flex-wrap: wrap;
			}
			
			button {
				cursor: pointer;
				min-width: rem(88);
				background-color: $gray;
				padding: rem(10);
				display: flex;
				flex-direction: column;
				align-items: center;
				border-radius: rem(20);

				@include min540 {
					margin-bottom: rem(20);
				}

				span:last-child {
					font-weight: 600;
				}

				&:hover {
					background-color: $blue2;
					color: white;
				}
			}

			button.active {
				background-color: $blue2;
				color: white;
			}
		}

		.field-search {
			margin-top: rem(14);
			.w-70 {
				flex: 0 0 70%;
				margin-left: rem(12);

				@include min540 {
					flex: 0 0 100%;
					margin-left: 0;
				}
			}

			@include min540 {
				flex-wrap: wrap;

				.custom-select {
					width: 100%;
					margin-bottom: rem(20);
				}
			}
		}

		.table {
			display: flex;
			flex-direction: column;
			margin-top: rem(20);

			@include min660 {
				flex-wrap: wrap;
				flex-direction: row;

				.w-45,
				.w-75,
				.w-120 {
					width: 100% !important;
				}

				.w-90 {
					width: auto !important;
				}

				.mr-10 {
					margin-right: 0;
				}
			}

			&-title {
				font-weight: 600;
				color: $blue2;
				display: block;
				margin-bottom: rem(12);
			}

			&-top {
				background-color: $gray;
				display: flex;
				flex-wrap: wrap;
				justify-content: space-between;
				border-radius: rem(3.5);
				padding: rem(8) rem(12);

				@include min660 {
					display: none;
				}
			}

			&-list {
				display: flex;
				// flex-wrap: wrap;
				justify-content: space-between;
				padding: rem(8) rem(12);

				@include min660 {
					flex-direction: column;
					// align-items: flex-end;
					margin-bottom: rem(20);
					padding: 0;
					display: block;
				}
			}

			&-button {
				padding: rem(8) rem(12);
				display: flex;
				justify-content: flex-end;

				margin-top: rem(30);
				
				@include min660 {
					width: 100%;
					justify-content: center;
				}
			}

			&-item {
				display: flex;
				flex-wrap: wrap;
				align-items: center;

				.item-smart {
					display: none;
				}

				@include min660 {
					// margin-bottom: rem(7);
					flex-wrap: nowrap;
					// justify-content: center;
					// align-items: flex-start;
					// display: block;

					.item-smart {
						display: flex;
						justify-content: center;
						align-items: center;
						width: rem(100);
						// flex: 0 0 rem(100);
						margin-right: rem(10);
						background-color: $gray;
						text-align: center;
						padding: rem(9) rem(10);
						// align-self: normal;
					}

					.py-10 {
						padding: rem(7) rem(12) rem(7) 0;

						.text {
							top: rem(20);
							right: rem(20);
						}
					}

					&:last-child {
						margin-bottom: 0;
					}
				}

				.filter {
					position: relative;
					display: flex;
					flex-direction: column;

					&::after {
						content: "\e901";
						font-family: 'icons';
						font-size: 4px;
					}
					&::before {
						content: "\e901";
						transform: rotate(180deg);
						font-family: 'icons';
						font-size: 4px;
					}
				}

				

				.text-blue {
					margin-right: rem(3);
				}
				
				
			}

			.table-item.flex-col {
				@include min660 {
					flex-direction: row;
				}
			}

			.first {
				flex:0 0 rem(97);

				@include min660 {
					flex: 0 0 100%;
				}
			}

			.nth-2 {
				min-width: rem(244);
				max-width: rem(360);
				margin-right: auto;

				@include min660 {
					min-width: 100%;
					max-width: 100%;
					margin-right: 0;
				}
				
			}

			.nth-3 {
				flex: 0 0 rem(134);
				margin-right: auto;
				text-align: center;

				@include min660 {
					flex: 0 0 100%;
					margin-right: 0;
				}
			}

			.last {
				margin-left: auto;
				flex: 0 0 rem(62);
				text-align: center;

				@include min660 {
					flex: 0 0 100%;
					margin-left: 0;
				}
			}
		}
	}
}

.author {
	display: flex;
	flex-wrap: wrap;
	align-items: center;

	@include min660 {
		flex-wrap: nowrap;
	}

	img {
		// align-self: flex-start;
		flex: 0 0 rem(35);
		margin-right: rem(10);

		@include min {
			flex: 0 0 rem(30);
			margin-right: rem(4);
		}
	}

	&-message {
		color: #636363;
		font-size: rem(14);
		font-weight: 300;
	}

	&-date {
		color: $blue2;
		font-size: rem(14);
		font-weight: 300;
	}

}

.message-block {
	background-color: white;
	border-radius: rem(5);
	border: rem(1) solid #dbdbdb;

	@include min {
		border: 0;
	}

	@include min660 {
		margin-left: rem(-10);
		margin-right: rem(-10);
	}
}
.message-wrap {
	padding: rem(8) rem(16);

	@include min660 {
		padding: rem(8);
	}

	.message-add {
		margin-top: auto;

		.field-file {
			margin-right: rem(20);
		}
	}
}

.author-head {
	padding: rem(8);
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	justify-content: space-between;
	background-color: #0085d4;
	border-radius: rem(5);

	

	.author {
		flex-grow: 1;
		margin-right: auto;

		color: white;

		.flex {
			> b {
				font-size: rem(18);
			}
		}

		i {
			margin-left: rem(6);
			font-size: rem(12);
		}
	}

	.warning-icon {
		margin-left: rem(10);
		font-size: rem(20);
		color: white;
	}

	.button-green {
		@include min660 {
			padding: rem(10) rem(10);
			span {
				display: none;
			}

			&::after {
				content: "ВКЛ";
			}
		}
	}
}

.message {
	display: flex;
	flex-direction: column;
	margin-bottom: rem(12);

	&:last-child {
		margin-bottom: 0;
	}

	&-top {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: center;

		@include min660 {
			>.text-blue {
				max-width: rem(80);
				font-size: rem(14);
			}
		}

		&_right {
			display: flex;
			flex-wrap: wrap;
			align-items: center;

			.b-text {
				margin-left: rem(8);

				@include min660 {
					margin-left: 0;
					align-self: flex-start;
					margin-top: rem(4);
				}
			}
		}

		img {
			margin-right: rem(8);
		}

		.title {
			font-weight: 600;
			margin-right: auto;
		}

		.text-blue {
			font-weight: 300;
		}
	}

	&-bottom {
		margin-top: rem(12);
		display: flex;
		// flex-wrap: wrap;
		align-items: center;

		.warning-icon {
			margin: 0 rem(10);

			@include min660 {
				align-self: flex-start;
			}

			i {
				color: $blue2;
				font-size: rem(24);
			}
		}

		.text {
			font-weight: 300;

			a {
				color: $blue;
			}
		}
	}
}

.message.gray {
	padding: rem(8);
	border-radius: rem(5);
	background-color: $gray;
}