.status {
	color: #03a507;
	font-size: rem(14);
	font-weight: 600;
}

.user-block_info {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	margin-bottom: rem(25);

	.left, .center, .right {
		width: 33.333%;
	}
	@include min660 {
		.left {
			width: 60%;
		}
		.center {
			width: 40%;
		}

		.right {
			align-items: center;
		}
	}

	.left {
		
		b, span {
			display: block;
			line-height: rem(24);
		}

		> b {
			// margin-bottom: rem(4);
		}
	}
	.center {
		> b {
			margin-bottom: rem(10);
			display: block;

			@include min660 {
				margin-bottom: 0;
			}
		}
	}

	.right {
		display: flex;
		// flex-wrap: wrap;
		justify-content: space-between;

		.rating-block {
			margin-right: rem(6);
		}

		a {
			color: $blue2;
			font-size: rem(14);
			flex: 0 0 rem(76);
		}
	}
}

.rating-block {
	display: flex;
	flex-wrap: wrap;
	flex: 0 0 rem(120);

	i {
		font-size: rem(19);
		margin-right: rem(4);
		margin-bottom: rem(5);

		&:nth-child(5n) {
			margin-right: 0;
		}
	}
}

.rating-count {
	display: flex;
	// flex-wrap: wrap;

	span {
		font-weight: 600;

		b {
			font-size: rem(36);
		}
	}
}

.form-pay {

	.title {
		font-weight: 600;
		font-size: rem(24);
		// margin-bottom: rem(4);
	}
}