.w-45 {
	width: rem(45) !important;
}
.w-75 {
	width: rem(90) !important;
}
.w-90 {
	width: rem(90) !important;
}
.w-120 {
	width: rem(120) !important;
}
.table-mod {

	.field {
		margin-bottom: 0 !important;
	}
	.table-list,
	.table-top {
		align-items: flex-start;
		justify-content: flex-start !important;
	}

	.label {
		font-size: rem(14);
		font-weight: 300;
		text-transform: lowercase;
		// text-align: right;
		// margin-bottom: rem(30);

		// @include min660 {
		// 	width: 100%;
		// }
	}

	.table-item {
		margin-right: rem(12);

		@include min660 {
			margin-right: 0;
		}

		&:last-child {
			margin-right: 0;
			flex-grow: 1;
		}
	}

	.items-end {
		@include min660 {
			align-items: flex-start !important;
		}
	}
}

.table-list-btn {
	justify-content: flex-end !important;
}