.pay-table {
	margin-bottom: rem(20);

	width: rem(350);
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;

	@include min660 {
		width: 100%;
		justify-content: flex-end;
		flex-direction: row-reverse;
	}

	
}

.pay-system_table {
	display: flex;
	flex-direction: column;

	@include min660 {
		// width: 100%;
		// margin-bottom: rem(20);


		> .flex {
			flex-direction: column;
		}

		&:last-of-type {
			margin-right: rem(20);
			margin-bottom: 0;
		}
	}

	b {
		margin-bottom: rem(5);
	}

	a {
		margin-left: rem(10);
		color: $blue2;

		@include min660 {
			margin-left: 0;
		}
	}
}