* {
	outline: none;
	border: 0;
}

html {
	font-size: 100%;
	height: 100%;

	@include w1200_w900 {
		font-size: 78%;
	}

	@include w768_w540 {
		font-size: 70%;
	}
}

button {
	background-color: transparent;
}

a {
	text-decoration: none;
}

li {
	list-style: none;
}

body {
	position: relative;
	font-family: 'Gilroy',sans-serif;
	font-weight: 400;
	font-size: rem(16);
	height: 100%;
	display: flex;
	flex-direction: column;
	// overflow: hidden;

	&::after {
		content: "";
		clip-path: polygon(100% 0, 38% 0, 100% 100%);
		width: 100%;
		height: 100%;
		// min-height: rem(1100);
		position: absolute;
		top: 0;
		left: 0;
		background-color: $gray;
		z-index: -2;

		@include min540 {
			clip-path: polygon(100% 0, -13% 0, 100% 79%);
		}
	}

	> .container {
		flex: 1 0 auto;
	}
}

.relative {
	position: relative;
}

.container {
	width: 1170px;
	margin: 0 auto;

	@include max {
		width: 1460px;
	}

	@include w1200_w900 {
		width: 900px;
	}

	@include min {
		width: calc(100% - 30px);
	}
}




.w-full {
	width: 100%;
}
.h-full {
	height: 100%;
}

@include min540 {
	.sm-w-full {
		width: 100% !important;
	}
	.sm-h-full {
		height: 100%;
	}
}

.flex {
	display: flex;
}
.flex-wrap {
	flex-wrap: wrap;
}
.flex-col {
	flex-direction: column;
}
.flex-end {
	justify-content: flex-end;
}
.flex-center {
	justify-content: center;
}
.flex-between {
	justify-content: space-between;
}
.items-center {
	align-items: center !important;
}
.items-end {
	align-items: flex-end !important;
}

@include min540 {
	.sm-flex-between {
		justify-content: space-between;
	}
}

.h-60 {
	height: rem(600);

	@include min {
		height: auto;
	}
}

.w-70 {
	width: 70%;

	@include min540 {
		width: 100%;
	}
}
.w-9\/12 {
	width: 75%;

	@include min768 {
		width: 100%;

		.w-3\/12,
		.w-8\/12 {
			width: 100%;
		}
	}

	@include ipad {
		width: 100%;

	}

	@include w1200_w900 {
		width: 100%;

	}
}
.w-8\/12 {
	width: 66.6666%;

	@include min768 {
		width: 100%;
	}
}
.w-7\/12 {
	width: 58.3333%;

	@include min660 {
		width: 100%;
	}
}
.w-6\/12 {
	width: 50%;

	@include min {
		width: 100%;
	}
}
.w-4\/12 {
	width: 25%;
}
.w-5\/12 {
	width: 41.6666%;

	@include min660 {
		width: 100%;
	}
}
.w-3\/12 {
	width: 33.333%;
	
}

.max-h-85 {
	@include mw900 {
		max-height: rem(850);
	}
}

.clip {
	clip-path: polygon(100% 0, 42% 0, 100% 86%);
	width: 100%;
	height: 100%;
	// max-height: rem(2000);
	position: absolute;
	top: 0;
	left: 0;
	background-color: $gray;
	z-index: -2;
}

.pl-32 {
	padding-left: rem(32);
}
.pr-32 {
	padding-right: rem(32);
}

.pl-16 {
	padding-left: rem(16);

	@include min {
		padding-left: 0;
	}
}
.pr-16 {
	padding-right: rem(16);

	@include min {
		padding-right: 0;
	}
}

.mt-auto {
	margin-top: auto;
}
.mt-10 {
	margin-top: rem(10);
}
.mt-20 {
	margin-top: rem(20);
}
.mt-30 {
	margin-top: rem(30);
}

.mb-10 {
	margin-bottom: rem(10);
}
.mb-20 {
	margin-bottom: rem(20);
}

.mb-25 {
	margin-bottom: rem(25);
}
.mr-10 {
	margin-right: rem(10);
}
.mr-20 {
	margin-right: rem(20);
}

.text-24 {
	font-size: rem(24);
	font-weight: 700;
}
.text-14 {
	font-size: rem(14);
	font-weight: 300;
}

.text-light {
	font-weight: 300;
}

.text-center {
	text-align: center;
}

.text-blue {
	color: $blue2;
}
.text-black {
	color: black;
}
.font-normal {
	font-style: normal !important;
}

.descr {
	font-size: rem(14);
}


.bg-gray {
	background-color: $gray;
	border-radius: rem(3.5);
	padding: rem(12);
}



.field {
	height: rem(42);
	border-radius: rem(5);
	background-color: white;
	border: rem(1) solid #dbdbdb;
	width: 100%;
	padding: 0 rem(10);
	color: #484848;
	font-size: rem(14);
	font-weight: 300;

	@include placeholder {
		color: #484848;
	}

	&:hover, &:focus {
		border-color: $blue2;
	}
}

.field-textarea {
	border-radius: rem(5);
	background-color: white;
	border: rem(1) solid #dbdbdb;
	width: 100%;
	padding: rem(10);
	color: #484848;
	font-size: rem(14);
	font-weight: 300;
	font-style: italic;
	resize: vertical;
	min-height: rem(100);
	max-height: rem(220);
}

.field-file {
	cursor: pointer;
	position: relative;

	&::after {
		content: "\e911";
		color: $blue2;
		font-size: rem(24);
		font-family: 'icons';
	}

	input {
		display: none;
	}
}

.field-icon {
	position: relative;

	.button {
		position: absolute;
		top: rem(14);
		right: rem(10);
	}

	
}

.fieldset-icon {
	position: relative;
	display: flex;
	align-items: center;
	margin-bottom: rem(14);

	.field {
		padding-left: rem(30);
	}

	i {
		position: absolute;
		left: rem(10);
	}
}

.field-group-text {
	position: relative;

	.field {
		padding-right: rem(20);

		@include min660 {
			padding-right: 0;
		}
	}

	.text {
		position: absolute;
		top: rem(12);
		right: rem(10);
		color: #484848;
		font-weight: 600;
		font-size: rem(14);
		text-transform: uppercase;
	}

	.button {
		position: absolute;
		top: 0;
		right: 0;
		height: 100%;
		width: rem(62);
		background-color: $gray;
		color: $blue2;
		font-size: rem(14);
		font-weight: 300;
		border-radius: rem(5);
	}

	.label {
		position: absolute;
		bottom: rem(-20);
		right: 0;

		@include min660 {
			right: auto;
			left: 0;
		}
	}
}

.fieldset {
	display: flex;
	flex-direction: column;

	label {
		font-size: rem(14);
		margin-bottom: rem(6);
	}

	.field {
		height: rem(36);
	}

	.text {
		color: #484848;
		font-size: rem(14);
		font-weight: 300;
		margin-top: rem(10);
	}
}


.button-blue {
	color: white;
	height: rem(42);
	padding: rem(10) rem(16);
	border-radius: rem(5);
	background-color: $blue2;
	font-weight: 300;
	display: inline-block;
	transition: all 500ms ease;
	// align-self: flex-start;

	@include w1200_w900 {
		padding: rem(8) rem(12);
	}

	@include min {
		padding: rem(8) rem(12);
	}

	&:hover {
		background-color: #0077be;
	}
}

.button-gray {
	color: black;
	height: rem(42);
	padding: rem(10) rem(16);
	border-radius: rem(5);
	background-color: #eeeded;
	font-weight: 300;
	display: inline-block;
	transition: all 500ms ease;
	// align-self: flex-start;

	@include w1200_w900 {
		padding: rem(8) rem(12);
	}

	@include min {
		padding: rem(8) rem(12);
	}

	&:hover {
		background-color: #dddddd;
	}
}

.button-green {
	color: white;
	height: rem(42);
	padding: rem(10) rem(16);
	border-radius: rem(5);
	background-color: $green-light;
	font-weight: 300;
	display: inline-block;
	transition: all 500ms ease;
	// align-self: flex-start;

	@include w1200_w900 {
		padding: rem(8) rem(12);
	}

	@include min {
		padding: rem(8) rem(12);
	}

	&:hover {
		background-color: #009404;
	}
}

.b-text {
	color: white;
	font-size: rem(11);
	font-weight: 300;
	padding: rem(6) rem(10);
	border-radius: rem(5);
	background-color: #0085d4;
}

.checkbox-switch {
	cursor: pointer;
	position: relative;
	width: rem(52);
	height: rem(21);
	border-radius: rem(3.5);
	background-color: #eeeded;

	input {
		display: none;
	}

	.check {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		right: auto;
		height: 100%;
		width: rem(24);
		background-color: #bebebe;
		
		border-radius: rem(3.5);
		transition: all 350ms ease;
	}

	input:checked ~ .check {
		right: 0;
		left: auto;
		background-color: #03a507;
	}
}

.page-title {
	font-size: rem(24);
	font-weight: 600;
	margin-bottom: rem(20);
}

.w-150 {
	width: rem(150);
	text-align: center;
}

.block-text {
	margin-top: rem(10);
	p {
		font-weight: 300;
		margin-bottom: rem(30);

		&:last-of-type {
			margin-bottom: 0;
		}
	}

	li {
		font-weight: 300;
	}
}

.absolute {
	position: absolute;
}

.mobile {
	@include min {
		display: none;
	}
}

@include min540 {
	.sm-flex-wrap {
		flex-wrap: wrap;
	}
}

@include min660 {
	.sm-flex-col {
		flex-direction: column;
	}
}

@include min660 {
	.sm-order-first {
		order: -9999;
	}

	.sm-mt-10 {
		margin-top: rem(10);
	}

	.sm-flex-start {
		justify-content: flex-start !important;
	}
}

.sticky {

	@include min768 {
		position: sticky;
		top: rem(70);
		margin-left: 100%;
	}

}