.center-wrap {
	border-radius: rem(5);
	background-color: white;
	border: rem(1) solid #dbdbdb;
	padding: rem(18) rem(12);

	> .title {
		text-align: center;
		font-size: rem(24);
		font-weight: 600;
		margin-bottom: rem(14);

		@include min660 {
			margin-bottom: rem(30);
		}
	}
	
}

@include min660 {
	.pay-system.pay-system-center {
		align-items: center;
		width: 100%;

		.button-blue {
			margin-left: 0;
		}
	}
}

.pay-system {
	margin-bottom: rem(26);

	display: flex;
	flex-direction: column;

	&:last-child {
		margin-bottom: 0;
	}
	> .label {
		line-height: rem(24);
		margin-bottom: rem(10);
		display: block;
	}

	&_items {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: space-between;
		margin-bottom: rem(10);

		button {
			height: rem(64);
			display: flex;
			align-items: center;
			padding: 0 rem(8);
			border-radius: rem(5);
			border: rem(1) solid transparent;
		}

		button.active {
			border: rem(1) solid #dbdbdb;
		}
	}

	&_form {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		margin-top: rem(24);
		

		.fieldset {
			width: calc(50% - 0.9375rem);
			
			@include min660 {
				width: 100%;
				margin-bottom: rem(20);

				&:last-of-type {
					margin-bottom: 0;
				}
			}
		}

		
	}

	.button-blue {
		margin-top: rem(20);
		margin-left: auto;
		width: rem(150);
	}
}