.header {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	padding-top: rem(9);
	padding-bottom: rem(18);
	


	.burger {
		display: none;
	}

	@include min {
		position: sticky;
		top: 0;
		justify-content: flex-start;
		background-color: $gray;
		padding: rem(9) 15px rem(18) 15px;
		margin-right: -15px;
		margin-left: -15px;
		z-index: 299;

		.burger {
			display: flex;
			align-items: center;
			flex-direction: column;
			justify-content: space-between;
			width: rem(32);
			height: rem(24);

			&::before {
				content: "";
				background-color: $blue2;
				border-radius: rem(3);
				height: rem(6);
				width: 100%;
				display: block;
				transition: all 500ms ease;
			}
			&::after {
				content: "";
				background-color: $blue2;
				border-radius: rem(3);
				height: rem(6);
				width: 100%;
				display: block;
				transition: all 500ms ease;
			}

			span {
				width: 100%;
				background-color: $blue2;
				border-radius: rem(3);
				height: rem(6);
				opacity: 1;
				transition: all 500ms ease;
			}
		}

		.burger.active {

			&::before {
				transform: rotate(45deg) translateY(0.5rem) translateX(0.2rem);
				
			}

			&::after {
				transform: rotate(-45deg) translateY(rem(-10)) translateX(rem(5));
			}

			span {
				opacity: 0;
			}
		}

		.header_logo {
			width: rem(195);
			margin-left: rem(16);

			img {
				background-size: 100%;
			}
		}
	}

	&_nav.active {
		transform: translateX(0);
	}

	&_nav {

		@include min {
			// display: none;
			transform: translateX(100%);
			transition: all 500ms ease;
			width: 80%;
			height: 100%;
			position: fixed;
			top: 0;
			right: 0;
			background-color: white;
			z-index: 300;
		}

		ul {
			display: flex;
			flex-wrap: wrap;

			@include min {
				flex-direction: column;
				margin: rem(20);
			}
			
			li.small {

				margin-right: rem(18);
			}

			li {
				position: relative;
				margin-right: rem(36);

				@include min {
					margin-right: 0;
					margin-bottom: rem(10);
				}

				&:last-child {
					margin-right: 0;
				}

				a {
					display: flex;
					align-items: center;

					@include min {
						font-size: rem(18);
					}

					i {
						position: absolute;
						left: rem(-24);
						top: rem(4);
						color: $blue;
						font-size: rem(18);

						@include min {
							display: none;
						}
					}

					&:hover {
						color: $blue;

						i {
							color: black;
						}
					}
				}
			}
		}
		
	}
}