.wrap_trade {
	position: relative;
	display: flex;
	flex-wrap: wrap;
	
	padding-left: rem(20);

	@include min540 {
		padding-left: 0;
		// position: sticky;
	}

	&-col {
		position: absolute;
		top: 0;
		left: rem(-20);
		text-align: center;
		margin-right: rem(16);

		// @include min540 {
		// 	&::after {
		// 		content: "...";
		// 		color: $blue2;
		// 	}
		// }

		@include min540 {
			// position: relative;
			left: auto;
			right: 0;
			
			// display: flex;
			// flex-direction: column;
			// align-items: center;
			margin-right: 0;
			// overflow: hidden;
			// margin-bottom: rem(25);
			
		}

		.top {
			margin-bottom: rem(12);

			// @include min540 {
			// 	margin-bottom: 0;
			// 	// margin-right: rem(16);
			// }

			i {
				font-size: rem(18);
				color: $blue2;

				@include min540 {
					font-size: rem(22);
				}
			}
		}

		.bottom {

			// @include min540 {
			// 	display: flex;
			// 	margin-right: rem(20);
			// 	// flex-wrap: wrap;
				
			// }
		}

		ul {
			@include min540 {
				overflow-x: scroll;
			}

			

			li {
				margin-bottom: rem(2);

				// @include min540 {
				// 	margin-bottom: 0;
				// 	margin-right: rem(14);
				// }

				&:last-child {
					margin-bottom: 0;
				}

				a:hover, a.active {
					color: $blue2;
				}
			}
		}
	}

	&-block {
		width: 50%;
		display: flex;
		flex-direction: column;

		// @include min540 {
		// 	width: 100%;
		// }

		@include min {
			width: 100%;
		}
		

		> .title {
			opacity: .75;
			display: block;
			margin-bottom: rem(5);
		}

		&_items {
			display: flex;
			flex-wrap: wrap;

			@include min {
				justify-content: space-between;
			}
		}
	}
}

.item-trade {
	width: rem(140);
	margin-right: rem(130);
	margin-bottom: rem(18);

	@include min540 {
		width: 48%;
		margin-right: 0;
	}

	@include min {
		width: 50%;
		margin-right: 0;
	}

	.title {
		font-size: rem(18);
		display: block;
		margin-bottom: rem(3);
	}

	
}

.link-list {
	display: flex;
	flex-wrap: wrap;

	li {
		margin-right: rem(5);
		margin-bottom: rem(5);

		&:last-child {
			margin-right: 0;
		}

		a {
			border-radius: rem(5);
			padding: rem(5);
			font-size: rem(14);
			background-color: $gray;
			display: block;


			&:hover {
				background-color: $blue;
				color: white;
			}
		}
	}
}

.link-blue {
	display: flex;
	flex-wrap: wrap;

	li {
		margin-right: rem(5);
		margin-bottom: rem(5);

		&:last-child {
			margin-right: 0;
		}

		a {
			color: $blue2;
			font-weight: 300;

			&:hover {
				text-decoration: underline;
			}
		}
	}
}

.break {
	flex-basis: 100%;
	height: 0;
}
.right-message {
	text-align: right;

	@include min {
		margin-bottom: rem(40);
	}

	img {
		width: 90%;
	}

	span {
		position: relative;
		display: flex;
		align-items: center;
		margin-top: rem(30);

		&::before {
			content: "";
			width: rem(57);
			height: rem(57);
			background-color: #c8c6c6;
			margin-left: 5%;
			border-radius: 100%;
		}

		&::after {
			content: "";
			position: absolute;
			width: 90%;
			// bottom: rem(-30);
			right: 0;
			height: rem(4);
			background-color: #c8c6c6;
			z-index: -3;
		}

		@include min {
			display: none;
		}
	}
}
.right-overlay-center {
	width: 100%;
	top: 0;
	right: 0;
	position: absolute;

		@include min {
			position: relative;
			right: 0;
			width: 130% !important;
			margin-bottom: rem(40);
		}
	

	span {
		display: flex;
		align-items: center;
		margin-top: rem(30);

		&::before {
			content: "";
			width: rem(57);
			height: rem(57);
			background-color: #c8c6c6;
			margin-left: 5%;
			border-radius: 100%;
		}

		&::after {
			content: "";
			position: absolute;
			width: 90%;
			// bottom: rem(-30);
			right: 0;
			height: rem(4);
			background-color: #c8c6c6;
			z-index: -3;
		}
	}
}
.right-overlay {
	width: 65%;
	top: rem(30);
	right: -16%;
	position: absolute;
	// z-index: -1;

	@include min {
		position: relative;
		right: 0;
		width: 100%;
		margin-bottom: rem(40);
	}

	span {
		display: flex;
		align-items: center;
		margin-top: rem(30);

		@include min {
			display: none;
		}

		&::before {
			content: "";
			width: rem(57);
			height: rem(57);
			background-color: #c8c6c6;
			margin-left: 0;
			border-radius: 100%;
		}
		&::after {
			content: "";
			position: absolute;
			width: 100%;
			// bottom: rem(-30);
			right: 0;
			height: rem(4);
			background-color: #c8c6c6;
			z-index: -3;
		}
	}
}
